import Slider from "react-slick";
import "slick-carousel/slick/slick.css";

const CHARTLIST = [
    { id: 1, tit: 'After LIKE', singer: "IVE(아이브)" },
    { id: 2, tit: 'Attention', singer: "NewJeans" },
    { id: 3, tit: 'Pink Venom', singer: "BLACKPINK" },
    { id: 4, tit: 'Hype boy', singer: "NewJeans" },
    { id: 5, tit: 'LOVE DIVE', singer: "IVE(아이브)" },
    { id: 6, tit: 'FOREVER 1', singer: "소녀시대(GIRLS' GENERATION" },
    { id: 7, tit: '그때 그 순간 그대로 (그그그)', singer: "WSG위너비(가야G)" },
    { id: 8, tit: 'Cookie', singer: "NewJeans" },
    { id: 9, tit: 'SNEAKERS', singer: "ITZY(있지)" },
    { id: 10, tit: '나의 X에게', singer: "경서" },
]

const Header = () => {
    return (
        <header className="Header">
            <div className="inner">
                <nav className="Gnb">
                    <ul className="menu">
                        <a href=""><li>코코넛 티켓</li></a>
                        <a href=""><li>이용권 구매</li></a>
                        <a href=""><li><i className="xi-user"></i> 로그인</li></a>
                    </ul>
                    <h1>
                        <a href="">
                            COCONUT
                        </a>
                    </h1>
                    <ul className="menu second">
                        <a href="/"><li>코코넛 라운지</li></a>
                        <a href="/"><li>이벤트</li></a>
                        <a href="/"><li>공지사항</li></a>
                    </ul>
                </nav>
            </div>
            <div className="search">
                <div className="container">
                    <Slider className="chart"
                        arrows={false}
                        autoplay={true}
                        autoplaySpeed={3000}
                        verticalSwiping={true}
                        vertical={true}>
                        {CHARTLIST.map((list, idx) => {
                            return (
                                    <figure key={idx}>
                                        {`${list.id}. ${list.tit} - ${list.singer}`}
                                    </figure>
                            )
                        })}

                    </Slider>
                    <form action="">
                        <input type="text" placeholder={`몽환적인 레트로 팝, 이창섭 "SUDDENLY"`}></input>
                        <button><i className="xi-search"></i></button>
                    </form>
                    <div className="event">
                    </div>
                </div>
            </div>

        </header>
    )
}
export default Header;